/**
 * 
 *  JSON.stringify
 */
function cache(f, funcName = f.name) {
    return (async (...arg) => {
        if (config.apiCache &&
            !f.noCache &&
            getCurrentEnv() === getCurrentEnv.env.siYuan) {
            const r = await f(...arg);
            const id = currentNodeId();
            if (id) {
                const key = getCacheKey(funcName, arg);
                const value = JSON.stringify(r);
                if (value.length <= config.maxCacheValueLength) {
                    setBlockAttrs({
                        id,
                        attrs: {
                            [key]: value,
                        },
                    });
                }
                else {
                    console.error(" config.maxCacheValueLength", value);
                }
            }
            return r;
        }
        else {
            if (getCurrentEnv() !== getCurrentEnv.env.siYuan) {
                return cacheExtract(funcName, arg);
            }
            else {
                return f(...arg);
            }
        }
    });
}
export function cacheWarp(p) {
    const warp = {};
    for (const key in p) {
        if (Object.prototype.hasOwnProperty.call(p, key)) {
            const element = p[key];
            if (typeof element === "function") {
                //@ts-ignore 
                warp[key] = cache(element, key);
            }
            else {
                warp[key] = element;
            }
        }
    }
    return warp;
}
