import { fetch_proxy } from "../util/fetch-aop";
/**
 *  post /api/query/sql 
 */
export function sqlQuery(/** sql  */ stmt) {
    const raw = JSON.stringify({
        stmt,
    });
    return fetch_proxy("/api/query/sql", { body: raw });
}
(function (sqlQuery) {
    sqlQuery.noCache = false;
})(sqlQuery || (sqlQuery = {}));
