import { htmlDecode } from "../util/index";
import { fetch_proxy } from "../util/fetch-aop";
import { sqlQuery } from "./sql";
//  htmlDecode  @link
htmlDecode.toString();
/**
 *  cache
 */
export function setBlockAttrs(p) {
    const raw = JSON.stringify(p);
    return fetch_proxy("/api/attr/setBlockAttrs", { body: raw });
}
(function (setBlockAttrs) {
    setBlockAttrs.noCache = true;
})(setBlockAttrs || (setBlockAttrs = {}));
/**
 * 
 *  cache   cache 
 * tips:  {@link htmlDecode util.htmlDecode}  {@link attrRow}  value 
 */
export async function getBlockAttrs(id) {
    const r = sqlQuery(`SELECT * FROM "attributes" WHERE block_id='${id}'`);
    return r;
}
(function (getBlockAttrs) {
    getBlockAttrs.noCache = false;
})(getBlockAttrs || (getBlockAttrs = {}));
/**  */
export async function getBlockAttr(id, name) {
    const attrs = (await getBlockAttrs(id)).data;
    return attrs.find((el) => el.name === name);
}
(function (getBlockAttr) {
    getBlockAttr.noCache = false;
})(getBlockAttr || (getBlockAttr = {}));
